.cc-component-placeholder {
    position: relative;
    display: flex;
    width: 100%;
    align-content: stretch;
    align-items: stretch;
    padding: 11px;
    border: 1px dashed #bfbfbf;
    box-sizing: border-box;

    &__content {
        width: 100%;
    }

    &:before {
        position: absolute;
        top: 10px;
        left: 10px;
        width: calc( 100% - ( 2 * 11px ) );
        height: calc( 100% - ( 2 * 11px ) );
        z-index: -1;
        border: 1px dashed rgba( #bfbfbf, .5 );
        content: ' ';
    }

    &--look_white-bg {
        background-color: #fff;
    }

    &__headline {
        margin: 0 0 1em;
        text-align: center;
        text-transform: uppercase;
    }
}
